import React from "react";
// import ReactPlayer from 'react-player'
import Videojs from './video.js';

// reactstrap components
import {Container} from "reactstrap";

// core components

function LandingPageHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth < 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  const videoJsOptions = {
      // fluid : true,
      width: 1000,
      autoplay: 'any',
      controls: true,
      // poster : "test.jpg",
      preload : 'auto',
      sources: [{
        src: 'videos/test2.mp4',
        type: 'video/mp4'
      }]
  }


  return (
    <>
      <div
        style={{
          backgroundImage:
            "url(" + require("assets/img/daniel-olahh.jpg") + ")",
        }}
        className="page-header"
        data-parallax={true}
        ref={pageHeader}
      >
        <div className="filter" />
        <Container>

          <div className="motto text-center">
            
            <div className="myplayer">
                <Videojs {...videoJsOptions} />
            </div>

            <h1>Peter, Hollywood Director</h1>
            <h3>I am an actor, acting teacher, writer and director living in Hollywood, California, USA. I was born in California, and I also lived in New York City for many years. I have been teaching Accent Reduction to actors here, who are then able to get booked for American roles. I have many interests including, but not limited to: History, Music, Physics, Health, Exercise, Photography, Filmmaking and Art.</h3>
            <br />
            
          </div>
        </Container>
      </div>
    </>
  );
}

export default LandingPageHeader;
